@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
/*==================== GOOGLE FONTS ====================*/

/*==================== VARIABLES CSS ====================*/
:root {
    --header-height: 3rem;

    /*========== Colors ==========*/
    /* Change favorite color */
    --hue-color: 230;
    /*Purple 250 - Green 142 - Blue 230 - Pink 340*/

    /* HSL color mode */
    --first-color: hsl(var(--hue-color), 69%, 61%);
    --first-color-second: hsl(var(--hue-color), 69%, 61%);
    --first-color-alt: hsl(var(--hue-color), 57%, 53%);
    --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
    --title-color: hsl(var(--hue-color), 8%, 15%);
    --text-color: hsl(var(--hue-color), 8%, 45%);
    --text-color-light: hsl(var(--hue-color), 8%, 65%);
    --input-color: hsl(var(--hue-color), 99%, 92%);
    --body-color: hsl(var(--hue-color), 60%, 99%);
    --container-color: #fff;
    --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
    --scroll-thumb-color: hsl(var(--hue-color), 12%, 80%);

    /*========== Font and typography ==========*/
    --body-font: 'Poppins', sans-serif;

    /* .5rem = 8px, 1rem = 16px, 1.5rem = 24px ... */
    --big-font-size: 2rem;
    --h1-font-size: 1.5rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: .938rem;
    --small-font-size: .813rem;
    --smaller-font-size: .75rem;

    /*========== Font weight ==========*/
    --font-medium: 500;
    --font-semi-bold: 600;

    /*========== Margenes Bottom ==========*/
    /* .25rem = 4px, .5rem = 8px, .75rem = 12px ... */
    --mb-0-25: 0.25rem;
    --mb-0-5: 0.5rem;
    --mb-0-75: 0.75rem;
    --mb-1: 0.1rem;
    --mb-1-5: 1.5rem;
    --mb-2: 2rem;
    --mb-2-5: 2.5rem;
    --mb-3: 3rem;

    /*========== z index ==========*/
    --z-tooltip: 10;
    --z-fixed: 100;
    --z-modal: 1000;
}

/* Font size for large devices */
@media screen and (min-width: 968px) {
    :root {
        --big-font-size: 3rem;
        --h1-font-size: 2.25rem;
        --h2-font-size: 1.5rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: .875rem;
        --smaller-font-size: .713rem;
    }
}

/*========== Variables Dark theme ==========*/
body.dark-theme {
    --first-color-second: hsl(var(--hue-color), 30%, 8%);
    --title-color: hsl(var(--hue-color), 8%, 95%);
    --text-color: hsl(var(--hue-color), 8%, 75%);
    --input-color: hsl(var(--hue-color), 29%, 16%);
    --body-color: hsl(var(--hue-color), 28%, 12%);
    --container-color: hsl(var(--hue-color), 29%, 16%);
    --scroll-bar-color: hsl(var(--hue-color), 12%, 48%);
    --scroll-thumb-color: hsl(var(--hue-color), 12%, 36%);

}



/*========== Button Dark/Light ==========*/
.nav__btns {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.change-theme {
    font-size: 1.25rem;
    color: hsl(230, 8%, 15%);
    color: var(--title-color);
    margin-right: 0.1rem;
    margin-right: var(--mb-1);
    cursor: pointer;
}

.change-theme:hover {
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
}

/*==================== BASE ====================*/
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    margin: 0 0 3rem 0;
    margin: 0 0 var(--header-height) 0;
    font-family: 'Poppins', sans-serif;
    font-family: var(--body-font);
    font-size: .938rem;
    font-size: var(--normal-font-size);
    background-color: hsl(230, 60%, 99%);
    background-color: var(--body-color);
    color: hsl(230, 8%, 45%);
    color: var(--text-color)
}

h1,
h2,
h3,
h4 {
    color: hsl(230, 8%, 15%);
    color: var(--title-color);
    font-weight: 600;
    font-weight: var(--font-semi-bold);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}

/*==================== REUSABLE CSS CLASSES ====================*/
.section {
    padding: 2rem 0 4rem;
}

.section__title {
    font-size: 1.5rem;
    font-size: var(--h1-font-size);
}

.section__subtitle {
    display: block;
    font-size: .813rem;
    font-size: var(--small-font-size);
    margin-bottom: 3rem;
    margin-bottom: var(--mb-3);
}

.section__title,
.section__subtitle {
    text-align: center;
}

/*==================== LAYOUT ====================*/

.container {
    max-width: 768px;
    margin-left: 1.5rem;
    margin-left: var(--mb-1-5);
    margin-right: 1.5rem;
    margin-right: var(--mb-1-5);
}

.grid {
    display: grid;
    grid-gap: 1.5rem;
    gap: 1.5rem
}

.header {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 100;
    z-index: var(--z-fixed);
    background-color: hsl(230, 60%, 99%);
    background-color: var(--body-color);
}

/*==================== NAV ====================*/
.nav {
    max-width: 968px;
    height: 3rem;
    height: var(--header-height);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.nav__logo,
.nav__toggle {
    color: hsl(230, 8%, 15%);
    color: var(--title-color);
    font-weight: 500;
    font-weight: var(--font-medium);
}

.nav__logo:hover {
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
}

.nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
}

.nav__toggle:hover {
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
}

@media screen and (max-width: 767px) {
    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: hsl(230, 60%, 99%);
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, .15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: 0.3s;
    }
}


.nav__list {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.nav__link {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    font-size: .813rem;
    font-size: var(--small-font-size);
    color: hsl(230, 8%, 15%);
    color: var(--title-color);
    font-weight: 500;
    font-weight: var(--font-medium);
    cursor: pointer;
}

.nav__link:hover {
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
}

.nav__icon {
    font-size: 1.2rem;
}

.nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: hsl(230, 69%, 61%);
    color: var(--first-color)
}

.nav__close:hover {
    color: hsl(230, 57%, 53%);
    color: var(--first-color-alt);
}

/* show menu */
.show-menu {
    bottom: 0;
}

/* Active link */

.active-link {
    color: hsl(230, 69%, 61%);
    color: var(--first-color)
}

/* Change background header */
.scroll-header {
    box-shadow: 0 -1px 4px rgba(0, 0, 0, .15);
}

/*==================== HOME ====================*/
.home__container {
    grid-gap: 1rem;
    gap: 1rem;
}

.home__content {
    grid-template-columns: .5fr 3fr;
    padding-top: 3.5rem;
    -webkit-align-items: center;
            align-items: center;
}

.home__social {
    display: grid;
    grid-template-columns: -webkit-max-content;
    grid-template-columns: max-content;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}

.home__social-icon {
    font-size: 1.25rem;
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
}

.home__social-icon:hover {
    color: hsl(230, 57%, 53%);
    color: var(--first-color-alt);
}

.home__img {
    width: 60%;
    margin: auto;
}

.home__img-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 50%;
}

.home__data {
    grid-column: 1/3;
}

.home__title {
    font-size: 2rem;
    font-size: var(--big-font-size);
}

.home__subtitle {
    font-size: 1.125rem;
    font-size: var(--h3-font-size);
    color: hsl(230, 8%, 45%);
    color: var(--text-color);
    font-weight: 500;
    font-weight: var(--font-medium);
    margin-bottom: 0.75rem;
    margin-bottom: var(--mb-0-75);
}

.home__description {
    margin-bottom: 2rem;
    margin-bottom: var(--mb-2);

}


.home__scroll-button {
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
    transition: .3s;
    cursor: pointer;
}

.home__scroll-button:hover {
    -webkit-transform: translateY(.25rem);
            transform: translateY(.25rem);
}

.home__scroll-mouse {
    font-size: 2rem;
}

.home__scroll-name {
    font-size: .813rem;
    font-size: var(--small-font-size);
    color: hsl(230, 8%, 15%);
    color: var(--title-color);
    font-weight: 500;
    font-weight: var(--font-medium);
    margin-right: 0.25rem;
    margin-right: var(--mb-0-25);
}

/*==================== BUTTONS ====================*/

.button {
    display: inline-block;
    background-color: hsl(230, 69%, 61%);
    background-color: var(--first-color);
    color: #fff;
    padding: 1rem;
    border-radius: .5rem;
    font-weight: 500;
    font-weight: var(--font-medium);
    cursor: pointer;
}

.button:hover {
    background-color: hsl(230, 57%, 53%);
    background-color: var(--first-color-alt);
}

.button__icon {
    font-size: 1.25rem;
    margin-left: 0.5rem;
    margin-left: var(--mb-0-5);
    transition: .3s;
}

.button__flex {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
}

/*==================== ABOUT ====================*/
.about__img {
    width: 200px;
    border-radius: .5rem;
    justify-self: center;
    -webkit-align-self: center;
            align-self: center;
}

.about__description {
    text-align: center;
    margin-bottom: 2.5rem;
    margin-bottom: var(--mb-2-5);
}

.about__info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-bottom: 2.5rem;
    margin-bottom: var(--mb-2-5);
}

.about__info-name {
    font-size: .75rem;
    font-size: var(--smaller-font-size);
}

.about__info-title {
    font-size: 1.25rem;
    font-size: var(--h2-font-size);
    font-weight: 600;
    font-weight: var(--font-semi-bold);
    color: hsl(230, 8%, 15%);
    color: var(--title-color);
}

.about__info-name,
.about__info-title {
    display: block;
    text-align: center;
}

.about__buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

/*==================== SKILLS ====================*/

.skills__container {
    grid-row-gap: 0;
    row-gap: 0;
}

.skills__header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 2.5rem;
    margin-bottom: var(--mb-2-5);
    cursor: pointer;
}

.skills__icon,
.skills__arrow {
    font-size: 2rem;
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
}

.skills__icon {
    margin-right: 0.75rem;
    margin-right: var(--mb-0-75);
}

.skills__title {
    font-size: 1.125rem;
    font-size: var(--h3-font-size);
}

.skills__subtitle {
    color: hsl(230, 8%, 65%);
    color: var(--text-color-light);
    font-size: .813rem;
    font-size: var(--small-font-size);
}

.skills__arrow {
    margin-left: auto;
    transition: .4s;
}

.skills__list {
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
    padding-left: 2.7rem;
}


.skills__titles {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 0.5rem;
    margin-bottom: var(--mb-0-5);
}

.skills__name {
    font-size: .938rem;
    font-size: var(--normal-font-size);
    font-weight: 500;
    font-weight: var(--font-medium);
}

.skills__bar,
.skills__pertange {
    height: 5px;
    border-radius: .25rem;
}

.skills__bar {
    background-color: hsl(230, 92%, 85%);
    background-color: var(--first-color-lighter);
}

.skills__pertange {
    display: block;
    background-color: hsl(230, 69%, 61%);
    background-color: var(--first-color);
}

.skills__close .skills__list {
    height: 0;
    overflow: hidden;
}

.skills__open .skills__list {
    height: -webkit-max-content;
    height: max-content;
    margin-bottom: 2.5rem;
    margin-bottom: var(--mb-2-5);
}

.skills__open .skills__arrow {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}


/*==================== QUALIFICATION ====================*/
.qualification__tabs {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-bottom: 2rem;
    margin-bottom: var(--mb-2);
}

.qualification__button {
    font-size: 1.125rem;
    font-size: var(--h3-font-size);
    font-weight: 500;
    font-weight: var(--font-medium);
    cursor: pointer
}

.qualification__button:hover {
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
}

.qualification__icon {
    font-size: 1.8rem;
    margin-right: 0.25rem;
    margin-right: var(--mb-0-25);
}

.qualification__data {
    display: grid;
    grid-template-columns: 1fr -webkit-max-content 1fr;
    grid-template-columns: 1fr max-content 1fr;
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
}

.qualification__title {
    font-size: .938rem;
    font-size: var(--normal-font-size);
    font-weight: 500;
    font-weight: var(--font-medium);
}

.qualification__subtitle {
    display: inline-block;
    font-size: .813rem;
    font-size: var(--small-font-size);
    margin-bottom: 0.1rem;
    margin-bottom: var(--mb-1);
}

.qualification__calendat {
    font-size: .75rem;
    font-size: var(--smaller-font-size);
    color: hsl(230, 8%, 65%);
    color: var(--text-color-light);
}

.qualification__rounder {
    display: inline-block;
    width: 13px;
    height: 13px;
    background-color: hsl(230, 69%, 61%);
    background-color: var(--first-color);
    border-radius: 50%;
}

.qualification__line {
    display: block;
    width: 1px;
    height: 100%;
    background-color: hsl(230, 69%, 61%);
    background-color: var(--first-color);
    -webkit-transform: translate(6px, -7px);
            transform: translate(6px, -7px);
}

.qualification [data-content] {
    display: none;
}

.qualification__active[data-content] {
    display: block;
}

.qualification__button.qualification__active {
    color: hsl(230, 69%, 61%);
    color: var(--first-color)
}


/*==================== PORTFOLIO ====================*/
.portfolio__container {
    overflow: visible;
    overflow: initial;
}

.portfolio__content {
    padding: 0 1.5rem;
}

.portfolio__img {
    width: 265px;
    border-radius: .5rem;
    justify-self: center;
    -webkit-align-self: center;
            align-self: center;
}

.portfolio__title {
    font-size: 1.125rem;
    font-size: var(--h3-font-size);
    margin-bottom: 0.5rem;
    margin-bottom: var(--mb-0-5);
}

.portfolio__description {
    margin-bottom: 0.75rem;
    margin-bottom: var(--mb-0-75);
}

.portfolio__button:hover .button__icon {
    -webkit-transform: translateX(.25rem);
            transform: translateX(.25rem);

}



/*==================== CONTACT ME ====================*/

.contact__container {
    grid-row-gap: 3rem;
    row-gap: 3rem;
}

.contact__information {
    display: -webkit-flex;
    display: flex;
    margin-bottom: 2rem;
    margin-bottom: var(--mb-2);
}

.contact__icon {
    font-style: 2rem;
    color: hsl(230, 69%, 61%);
    color: var(--first-color);
    margin-right: 0.75rem;
    margin-right: var(--mb-0-75);
}

.contact__title {
    font-size: 1.125rem;
    font-size: var(--h3-font-size);
    font-weight: 500;
    font-weight: var(--font-medium);
}

.contact__subtitle {
    font-size: .813rem;
    font-size: var(--small-font-size);
    color: hsl(230, 8%, 65%);
    color: var(--text-color-light);
}

.contact__content {
    background-color: hsl(230, 99%, 92%);
    background-color: var(--input-color);
    border-radius: .5rem;
    padding: .75rem 1rem .25rem;
}

.contact__label {
    font-size: .75rem;
    font-size: var(--smaller-font-size);
    color: hsl(230, 8%, 15%);
    color: var(--title-color);
}

.contact__input {
    width: 100%;
    background-color: hsl(230, 99%, 92%);
    background-color: var(--input-color);
    color: hsl(230, 8%, 45%);
    color: var(--text-color);
    font-family: 'Poppins', sans-serif;
    font-family: var(--body-font);
    font-size: .938rem;
    font-size: var(--normal-font-size);
    border: none;
    outline: none;
    padding: .25rem .5rem .5rem 0;

}

/*==================== FOOTER ====================*/

.footer {
    padding-top: 2rem;
}

.footer__container {
    grid-row-gap: 3.5rem;
    row-gap: 3.5rem;
}

.footer__bg {
    background-color: hsl(230, 69%, 61%);
    background-color: var(--first-color-second);
    padding: 2rem 0 3rem;
}

.footer__title {
    font-size: 1.5rem;
    font-size: var(--h1-font-size);
    margin-bottom: 2.5rem;
    margin-bottom: var(--mb-2-5);
}

.footer__subtitle {
    font-size: .813rem;
    font-size: var(--small-font-size);
}

.footer__links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
}

.footer__link:hover {
    color: hsl(230, 92%, 85%);
    color: var(--first-color-lighter);
}

.footer__social {
    font-size: 1.25rem;
    margin-right: 1.5rem;
    margin-right: var(--mb-1-5);
}

.footer__social:hover {
    color: hsl(230, 92%, 85%);
    color: var(--first-color-lighter);
}

.footer__copy {
    font-size: .75rem;
    font-size: var(--smaller-font-size);
    text-align: center;
    color: hsl(230, 8%, 65%);
    color: var(--text-color-light);
    margin-top: 3rem;
    margin-top: var(--mb-3);
    padding-bottom: 2rem;
    padding-bottom: var(--mb-2);
}

.footer__title,
.footer__subtitle,
.footer__link,
.footer__social {
    color: #fff
}




/*========== SCROLL UP ==========*/
.scrollup {
    position: fixed;
    right: 1rem;
    bottom: -20%;
    background-color: hsl(230, 69%, 61%);
    background-color: var(--first-color);
    opacity: .8;
    padding: 0 .3rem;
    border-radius: .4rem;
    z-index: 10;
    z-index: var(--z-tooltip);
    transition: .4s;
    cursor: pointer;
}

.scrollup:hover {
    background-color: hsl(230, 57%, 53%);
    background-color: var(--first-color-alt);
}

.scrollup__icon {
    font-size: 1.5rem;
    color: #fff;
}

/* Show scroll */
.show-scroll {
    bottom: 5rem;
}


/*========== SCROLL BAR ==========*/
::-webkit-scrollbar {
    width: .60rem;
    background-color: hsl(230, 12%, 90%);
    background-color: var(--scroll-bar-color);
    border-radius: .5rem;
}

::-webkit-scrollbar-thumb {
    background-color: hsl(230, 12%, 80%);
    background-color: var(--scroll-thumb-color);
    border-radius: .5rem;
}

::-webkit-scrollbar-thumb:hover {
    background-color: hsl(230, 8%, 65%);
    background-color: var(--text-color-light);
}


/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width:350px) {
    .container {
        margin-left: 0.1rem;
        margin-left: var(--mb-1);
        margin-right: 0.1rem;
        margin-right: var(--mb-1);
    }

    .nav__menu {
        padding: 2rem .25rem 4rem;
    }

    .nav__list {
        grid-column-gap: 0;
        -webkit-column-gap: 0;
                column-gap: 0;
    }

    .home__content {
        grid-template-columns: .25fr 3fr;
    }

    .home__img {
        width: 180px;
    }

    .skills__title {
        font-size: .938rem;
        font-size: var(--normal-font-size);
    }

    .qualification__data {
        grid-gap: .5rem;
        gap: .5rem
    }
